<template>
  <div>
    <div class="btns">
      <router-link to="/admin/list" style="margin-right:10px;">
        <el-button type="info" icon="el-icon-close" plain>退出</el-button>
      </router-link>
      <el-button
        v-if="action == 'edit' || action == 'add'"
        type="primary"
        icon="el-icon-finished"
        plain
        @click="savePost"
        >保存</el-button
      >
    </div>
    <el-form ref="postForm" label-width="50px" label-position="left">
      <el-row class="form-row">
        <el-col :span="5">
          <el-form-item label="标题">
            <el-input v-model="name" placeholder="文章标题"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="3" :offset="1">
          <el-form-item label="分类">
            <el-select v-model="category" placeholder="文章类型">
              <el-option
                :label="item"
                :value="item"
                v-for="item in categories"
                :key="item"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="9" :offset="1">
          <el-form-item label="简介">
            <el-input v-model="description" placeholder="文章简介"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="2" :offset="1">
          <el-form-item label="发布">
            <el-checkbox v-model="isPublish" />
          </el-form-item>
        </el-col>
        <el-col :span="2">
          <el-form-item label="推荐">
            <el-checkbox v-model="isRecommend" />
          </el-form-item>
        </el-col>
      </el-row>
      <div id="vditor" class=" markdown-body vditor" />
    </el-form>
  </div>
</template>

<script>
const { siteOptions } = require("@/settings");
import Vditor from "vditor";
import defaultText from "@/config/default";
import { base64ToUtf8 } from "@/utils/common";
import { githubApi } from "@/utils/github-api";

import "vditor/dist/index.css";

export default {
  beforeRouteEnter(to, from, next) {
    switch (to.params.action) {
      case "add":
        document.title = `${to.meta.title}-创建`;
        localStorage.setItem("vditorvditor", "");
        break;
      case "edit":
        document.title = `${to.meta.title}-编辑`;
        break;
      default:
        document.title = `${to.meta.title}-查看`;
    }
    next();
  },
  data() {
    return {
      isMobile: window.innerWidth <= 960,
      vditor: null,
      path: this.$route.query.path,
      action: this.$route.params.action,
      id: this.$route.query.id || "",
      name: this.$route.query.name,
      category: this.$route.query.category || "",
      description: this.$route.query.description || "",
      isRecommend: this.$route.query.isRecommend === "true",
      isPublish: this.$route.query.isPublish === "true",
      post: {},
      postList: {},
      categories: [],
      author: siteOptions.author || "",
    };
  },
  created() {
    this.setDefaultText();
  },
  mounted() {
    this.initVditor();
    this.getPost(this.path);
    this.getPostList();
    this.getCategory();
  },
  methods: {
    initVditor() {
      const that = this;
      const options = {
        width: this.isMobile ? "100%" : "80%",
        height: "0",
        tab: "\t",
        // counter: "999999",
        typewriterMode: true,
        mode: "wysiwyg",
        // preview: {
        //   delay: 100,
        //   show: !this.isMobile,
        // },
        counter: {
          enable: true,
          type: "text",
        },
        outline: {
          enable: true,
          position: "right",
        },
        upload: {
          max: 5 * 1024 * 1024,
          // linkToImgUrl: 'https://sm.ms/api/upload',
          handler(file) {
            let formData = new FormData();
            for (let i in file) {
              formData.append("smfile", file[i]);
            }
            let request = new XMLHttpRequest();
            request.open("POST", "https://sm.ms/api/upload");
            request.onload = that.onloadCallback;
            request.send(formData);
          },
        },
      };
      this.vditor = new Vditor("vditor", options);
    },
    onloadCallback(oEvent) {
      const currentTarget = oEvent.currentTarget;
      if (currentTarget.status !== 200) {
        return this.$message({
          type: "error",
          message: currentTarget.status + " " + currentTarget.statusText,
        });
      }
      let resp = JSON.parse(currentTarget.response);
      let imgMdStr = "";
      if (resp.code === "invalid_source") {
        return this.$message({
          type: "error",
          message: resp.message,
        });
      }
      if (resp.code === "image_repeated") {
        imgMdStr = `![](${resp.images})`;
      } else if (resp.code === "success" || resp.success) {
        imgMdStr = `![${resp.data.filename}](${resp.data.url})`;
      }
      this.vditor.insertValue(imgMdStr);
    },
    setDefaultText() {
      const savedMdContent = localStorage.getItem("vditorvditor") || "没有数据";
      if (!savedMdContent.trim()) {
        localStorage.setItem("vditorvditor", defaultText);
      }
    },
    async getPost(path) {
      if (!path) return;
      this.$loading.show();
      const file = await githubApi.getFile(path);
      this.$loading.hide();
      if (!file) {
        this.$message.error("No data");
        return;
      }
      this.post.content = base64ToUtf8(file.content);
      this.post.sha = file.sha
      this.vditor.setValue(this.post.content);
    },
    async getPostList() {
      this.$loading.show();
      const file = await githubApi.getFile(`db/_post/postList.json`);
      this.$loading.hide();
      if (!file) {
        this.$message.error("No data");
        return;
      }
      this.postList = JSON.parse(base64ToUtf8(file.content));
      this.postList.sha = file.sha;
    },
    async getCategory() {
      this.$loading.show();
      const file = await githubApi.getJsonRawFile("db/_post/category.json");
      this.$loading.hide();
      if (!file) {
        this.$message.error("No data");
        return;
      }
      this.categories = file.data;
    },
    async savePost() {
      this.post.content = this.vditor.getValue();
      if (this.action == "edit") {
        await this.updatePost();
        return;
      }
      this.$loading.show();
      const res = await githubApi.addFile(
        `db/_post/list/${this.name}.md`,
        this.post.content
      );
      if (res.status !== 'OK') {
        this.$loading.hide();
        this.$message.error(res.msg);
        return;
      }
      this.postList.data.unshift({
        name: this.name,
        path: `db/_post/list/${this.name}.md`,
        category: this.category,
        description: this.description,
        isRecommend: this.isRecommend,
        isPublish: this.isPublish,
        author: this.author,
        cover: "",
        date: new Date(),
      });
      console.log("savePost")
      const res2 = await githubApi.updateFile(
        this.postList.path,
        this.postList.sha,
        JSON.stringify(this.postList)
      );
      this.$loading.hide();
      if (res2.status !== 'OK') {
        this.$message.error(res2.msg);
        return;
      }
      this.$message(res2.msg);
      await this.$router.push("/admin/list");
    },
    async updatePost() {
      this.$loading.show();
      const res = await githubApi.updateFile(
        `db/_post/list/${this.name}.md`,
        this.post.sha,
        this.post.content
      );
      console.log(res);

      if (res.status !== 'OK') {
        this.$loading.hide();
        this.$message.error(res.msg);
        return;
      }
      console.log("updatePost")
      console.log(this.postList)
      this.postList.data = this.postList.data.map((item) => {
        if (item.path == this.path) {
          return {
            ...item,
            name: this.name,
            description: this.description,
            isRecommend: this.isRecommend,
            isPublish: this.isPublish,
            category: this.category,
            cover: "",
          };
        }
        return item;
      });
      const res2 = await githubApi.updateFile(
        this.postList.path,
        this.postList.sha,
        JSON.stringify(this.postList)
      );
      this.$loading.hide();
      if (res2.status !== 'OK') {
        this.$message.error(res2.msg);
        return;
      }
      this.$message(res2.msg);
      await this.$router.push("/admin/list");
    },
  },
};
</script>

<style lang="scss" scoped>
.vditor {
  position: absolute;
  width: 80%;
  text-align: left;
}
.vditor-reset {
  font-size: 14px;
}
.vditor-textarea {
  font-size: 14px;
  height: 100% !important;
}
.btns {
  font-size: 12px;
  padding-bottom: 20px;
  margin-bottom: 15px;
  margin-top: 5px;
  position: relative;
  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background: rgba(69, 76, 82, 0.2);
  }
  button {
    padding: 8px 10px;
  }
}

.form-row {
  height: 60px;
  width: 100%;
  margin: 0 0 10px;
  .el-textarea__inner {
    resize: none;
  }
}

.markdown-body {
  height: calc(100vh - 245px)!important;
}
</style>
